*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: cadetblue;
}
.wraper{
    
    display: flex;
    justify-content: center;
    text-align: center;
    position: relative;
    top:35vh
    
   
}


 #box{

    padding: 12px;
    margin: 5px;
    width: 30em;
    border-radius: 10px;
    background-color: burlywood;
 }
 .remember-forget{
    font-size: 24px;
 }
 .btn{
    padding: 8px 42px;
    border-radius: 9px;
    background-color: rosybrown;
    margin: 20px;
    outline: none;
}
 input #box::placeholder{
    font-size: 102px;
 }
 input #box.icon{
    position: absolute;
    right: 30px;
 }